@import url(https://fonts.googleapis.com/css?family=Manrope:200,400,700&display=swap);
body {
  font-weight: 300;
  font-family: 'Manrope', sans-serif;
}

h1, h2, h3, h4, strong {
  font-weight: 400;
}

h1 {
  margin-bottom: 30px;
}

a, a:hover {
  color: inherit;
  text-decoration: none;
}
.navbar {
  margin-bottom: 40px
}
.navbar img{
  width:100px;
}
.nav-logout{
  cursor:pointer;
}
.navbar-nav{
  width:100%;
}


.toast {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 99999;
  width: 350px
}

.error-message {
  background: red;
  color: white;
  padding: 10px
}
.games-list .card{   
    margin-bottom: 30px;
}

.games-list img {
    height: 160px;
    object-fit: contain;
}
.game-details img {
    width: 100%
}
.game-details{
    margin-bottom: 50px;
}
.image-button-game-detail{
    display:flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 50px;
}
.image-button-game-detail img{
    margin-bottom: 50px;
}

.name-and-image{
    display:flex;
    align-items:center;
    margin-bottom: 16px;
}
.user-image{
    width: 50px;
    border-radius: 50%;
    margin: 0 24px 0 0;
}
article{
    margin: 16px;
}
.class-card{
    padding: 0;
}
#map{
    width: 100%;
    height: 400px;
}

